body {
  margin: 0;
  background-color: rgb(82, 81, 81);
}

.topnav {
  background-color: #333;
  overflow: hidden;
  border-bottom: solid black;
}

.topnav a {
  float: left;
  color: white;
  text-align: center;
  padding: 30px 20px;
  text-decoration: none;
  font-size: 20px;
}

.topnav a:hover {
  background-color: #ddd;
  color: black;
}

.topnav a.active {
  background-color: #00aacc;
  color: white;
}